body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/********** Base **********/

* {
  box-sizing: border-box;
  text-decoration: none;
}

html {
  font-family: Arial, serif;
  color: black;
}

/* img { */

/* width: 100%; */

/* } */

.App {
  text-align: center;
}

/********** Nav **********/

.Nav_menu {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 100%;
  height: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.Nav_menu ul {
  margin: 0 auto;
  padding: 0;
}

.Nav_link:link {
  color: black;
  font-size: 1.2em;
  text-decoration: none;
  padding-right: 10px;
}

.Nav_link:hover {
  color: yellow;
}

.Nav_link:active {
  color: teal;
}

.activeRoute {
  color: red;
}

/**************** Main ****************/

/********** Edit **********/

.contact-container {
  max-width: 80%;
  margin: 0 auto;
}

.contact-container>p {
  margin: 6px 0;
}

.contact-form>input:not(:last-child) {
  margin-bottom: 6px;
  font-size: 1.1em;
  padding: 4px;
  border: 1px solid #616161;
}

.contact-form>input:last-child {
  cursor: pointer;
  margin: 14px 0 4px 0;
  font-size: 1.1em;
  padding: 4px 0;
  border: none;
  background-color: rgb(255, 161, 73);
  transition: background-color .3s;
}
.contact-form>input:last-child:hover {
  background-color: rgb(248, 153, 64);
}

.contact-form>input:not(:last-child):focus {
  background-color: rgb(255, 243, 231);
  outline: 1px solid rgba(0, 0, 0, 0);
}

/**************** Helpers ****************/

/********** Flex **********/

.flex {
  display: -webkit-flex;
  display: flex;
}

.space-between {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.space-even {
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.space-around {
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.space-center {
  -webkit-justify-content: center;
          justify-content: center;
}

.space-start {
  -webkit-justify-content: start;
          justify-content: start;
}

.space-end {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.align-center {
  -webkit-align-items: center;
          align-items: center;
}

.flex.wrap {
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.flex-col {
  -webkit-flex-direction: column;
          flex-direction: column;
}

.flex-reverse {
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
}

.flex-child {
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
}

/********** Lists **********/

.clean-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.align-left {
  text-align: left;
}

.text-wrap {
  overflow-wrap: break-word;
  word-wrap: break-word;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}
